import { defineStore } from "pinia";
import axios from 'axios'

export const useGetDataStore = defineStore('GetData', {

    actions : {
        async getAlbumData(requestNumber) {
            
            let targetURL = 'https://jsonplaceholder.typicode.com/albums/' + requestNumber;                        
            const response = (await axios.get(targetURL)).data;
            return response;
        }

    }
})